<template>
    <div class="UserInfo">
        <div class="loading" v-if="isLoading">
            <img src="../assets/loading.gif">
        </div>
        <div  class="userInfomation" v-else>
            <section>
                <img :src="user_info.avatar_url" >
                <span>{{user_info.loginname}}</span>
                <p>
                    {{user_info.score}}积分
                </p>
                <p>
                    注册时间：{{user_info.create_at | formatDate}}
                </p>
            </section>
            <div class="replies">
                <p>回复主题</p>
                <ul>
                    <li v-for="item in user_info.recent_replies" v-bind:key="item">
                        <router-link :to="{
                            name:'post_content',
                            params:{
                                id: item.id
                            }
                        }">{{item.title}}</router-link>
                    </li>
                </ul>
            </div>
            <div class="topics">
                <p>创建的主题</p>
                <ul>
                    <li v-for="item in user_info.recent_topics" v-bind:key="item">
                        <router-link :to="{
                          name:'post_content',
                          params:{
                            id:item.id
                          }
                          }">
                            {{item.title}}
                        </router-link>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "User_info",
        data(){
            return {
                isLoading:false,
                user_info:[]
            }
        },
        methods:{
            getUserData(){
                this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
                    .then(res=>{
                        if(res.data.success == true){
                            this.isLoading = false
                            this.user_info = res.data.data
                        }
                    })
                    .catch(err=>{
                        alert("userinfo 获取数据出错")
                        console.log(err)
                    })
            }
        },
        beforeMount() {
            this.isLoading=true
            this.getUserData()
        }
    }
</script>

